import * as React from 'react'; 
import { getEventById } from '@/lib/data-service';
import type { EventData } from '@/lib/types';
import Image from 'next/image';
import { CalendarDays, MapPin, DollarSign, Ticket, Users, Building, Home as HomeIcon, Info } from 'lucide-react'; // Added HomeIcon and Info
import PurchaseForm from '@/components/purchase-form';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';

interface EventPageParams {
  params: { id: string };
}

export default async function EventPage({ params }: EventPageParams) {
  const event = await getEventById(params.id);

  if (!event) {
    return (
      <div className="text-center py-12">
        <h1 className="text-3xl font-bold text-destructive">Evento nÃ£o encontrado</h1>
        <p className="text-muted-foreground mt-2">O evento que vocÃª estÃ¡ procurando nÃ£o existe ou foi removido.</p>
      </div>
    );
  }

  const eventDate = new Date(event.data_horario);
  const timeZone = 'America/Sao_Paulo'; 

  const formattedDate = eventDate.toLocaleDateString('pt-BR', {
    day: '2-digit',
    month: 'long',
    year: 'numeric',
    timeZone,
  });
  const formattedTime = eventDate.toLocaleTimeString('pt-BR', {
    hour: '2-digit',
    minute: '2-digit',
    timeZone,
  });

  const isSoldOut = event.quantidade_disponivel <= 0;

  return (
    <div className="container mx-auto max-w-4xl py-8 px-4">
      <Card className="overflow-hidden shadow-xl">
        <div className="relative h-72 w-full">
          <Image
            src={event.imagem_url || `https://picsum.photos/seed/${event.id}/800/400`}
            alt={event.nome_evento}
            layout="fill"
            objectFit="cover"
            priority
            data-ai-hint="event detail banner"
          />
        </div>
        <CardHeader className="p-6">
          <CardTitle className="text-4xl font-bold text-primary">{event.nome_evento}</CardTitle>
        </CardHeader>
        <CardContent className="p-6 space-y-6">
          <p className="text-lg text-foreground leading-relaxed">{event.descricao}</p>
          
          <Separator />

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-foreground">
            <div className="space-y-3">
              <InfoItem icon={<CalendarDays />} label="Data e Hora" value={`${formattedDate} Ã s ${formattedTime}`} />
              {event.nome_local_evento && <InfoItem icon={<HomeIcon />} label="Nome do Local" value={event.nome_local_evento} />}
              {event.cep && <InfoItem icon={<Info />} label="CEP" value={event.cep} />}
              <InfoItem icon={<MapPin />} label="EndereÃ§o" value={event.local} />
              <InfoItem icon={<Building />} label="Cidade" value={event.cidade} />
            </div>
            <div className="space-y-3">
              <InfoItem icon={<DollarSign />} label="PreÃ§o" value={`R$ ${event.preco_ingresso.toFixed(2).replace('.', ',')}`} />
              <InfoItem icon={<Users />} label="Ingressos DisponÃ­veis" value={`${event.quantidade_disponivel}`} badge={isSoldOut ? "Esgotado" : (event.quantidade_disponivel < 20 ? "Ãltimos ingressos" : undefined)} badgeVariant={isSoldOut ? "destructive" : (event.quantidade_disponivel < 20 ? "default" : undefined)} />
            </div>
          </div>

          <Separator />
          
          {isSoldOut ? (
            <div className="text-center py-6 bg-muted rounded-md">
              <p className="text-2xl font-semibold text-destructive">Ingressos Esgotados!</p>
              <p className="text-muted-foreground">NÃ£o hÃ¡ mais ingressos disponÃ­veis para este evento.</p>
            </div>
          ) : (
            <PurchaseForm event={event} />
          )}

        </CardContent>
      </Card>
    </div>
  );
}

interface InfoItemProps {
  icon: React.ReactNode;
  label: string;
  value: string;
  badge?: string;
  badgeVariant?: "default" | "secondary" | "destructive" | "outline";
}

function InfoItem({ icon, label, value, badge, badgeVariant }: InfoItemProps) {
  return (
    <div className="flex items-start">
      <span className="text-primary mt-1 mr-3 shrink-0">{React.cloneElement(icon as React.ReactElement, { className: 'h-6 w-6' })}</span>
      <div>
        <p className="font-semibold text-foreground">{label}</p>
        <p className="text-muted-foreground">{value} {badge && <Badge variant={badgeVariant} className="ml-2">{badge}</Badge>}</p>
      </div>
    </div>
  );
}
